import { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { supabase } from "@/integrations/supabase/client";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Card, CardContent } from "@/components/ui/card";
import { Skeleton } from "@/components/ui/skeleton";
import { Button } from "@/components/ui/button";
import { ScrollArea } from "@/components/ui/scroll-area";
import { 
  Users, 
  Briefcase, 
  Shield, 
  Mail, 
  Phone, 
  Calendar,
  Clock,
  MapPin,
  Star,
  Heart,
  MessageSquare,
  Store,
  ChevronRight,
  ArrowLeft,
  ExternalLink,
  FileText,
  User
} from "lucide-react";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { formatDistanceToNow } from "date-fns";

interface UserProfileDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  userId: string;
  userName: string;
  userRole: string;
}

interface UserProfileData {
  id: string;
  display_name: string;
  avatar_url: string | null;
  phone: string | null;
  created_at: string;
  language_preference: string | null;
  activity_terms_agreed: boolean | null;
  activity_terms_agreed_at: string | null;
  is_banned: boolean | null;
  ban_reason: string | null;
  // Invitation code application fields
  full_name: string | null;
  nif: string | null;
  contact: string | null;
  address: string | null;
}

interface UserStats {
  favoriteRestaurants: number;
  favoriteActivities: number;
  comments: number;
  ratings: number;
  addresses: number;
  restaurants: number;
}

type DetailView = null | "restaurants" | "activities" | "comments" | "ratings" | "addresses" | "managedRestaurants";

export function UserProfileDialog({
  open,
  onOpenChange,
  userId,
  userName,
  userRole,
}: UserProfileDialogProps) {
  const { t } = useTranslation();
  const { getLocalizedField } = useLocalizedContent();
  const [isLoading, setIsLoading] = useState(true);
  const [profile, setProfile] = useState<UserProfileData | null>(null);
  const [email, setEmail] = useState<string | null>(null);
  const [stats, setStats] = useState<UserStats>({
    favoriteRestaurants: 0,
    favoriteActivities: 0,
    comments: 0,
    ratings: 0,
    addresses: 0,
    restaurants: 0,
  });
  const [detailView, setDetailView] = useState<DetailView>(null);
  const [detailData, setDetailData] = useState<any[]>([]);
  const [detailLoading, setDetailLoading] = useState(false);

  useEffect(() => {
    if (open && userId) {
      fetchUserProfile();
    }
  }, [open, userId]);

  const fetchUserProfile = async () => {
    setIsLoading(true);
    try {
      // Fetch profile data
      const { data: profileData, error: profileError } = await supabase
        .from("profiles")
        .select("*")
        .eq("id", userId)
        .maybeSingle();

      if (profileError) throw profileError;
      setProfile(profileData);

      // Fetch email using edge function
      try {
        const { data: emailData } = await supabase.functions.invoke("get-user-emails", {
          body: { userIds: [userId] },
        });
        if (emailData?.emails?.[userId]) {
          setEmail(emailData.emails[userId]);
        }
      } catch (e) {
        console.error("Error fetching email:", e);
      }

      // Fetch stats in parallel
      const [
        favoritesResult,
        subscriptionsResult,
        commentsResult,
        ratingsResult,
        addressesResult,
        restaurantsResult,
      ] = await Promise.all([
        supabase.from("favorites").select("id", { count: "exact", head: true }).eq("user_id", userId),
        supabase.from("subscriptions").select("id", { count: "exact", head: true }).eq("user_id", userId),
        supabase.from("comments").select("id", { count: "exact", head: true }).eq("user_id", userId),
        supabase.from("ratings").select("id", { count: "exact", head: true }).eq("user_id", userId),
        supabase.from("user_addresses").select("id", { count: "exact", head: true }).eq("user_id", userId),
        supabase.from("merchant_restaurants").select("id", { count: "exact", head: true }).eq("user_id", userId),
      ]);

      setStats({
        favoriteRestaurants: favoritesResult.count || 0,
        favoriteActivities: subscriptionsResult.count || 0,
        comments: commentsResult.count || 0,
        ratings: ratingsResult.count || 0,
        addresses: addressesResult.count || 0,
        restaurants: restaurantsResult.count || 0,
      });
    } catch (error) {
      console.error("Error fetching user profile:", error);
    } finally {
      setIsLoading(false);
    }
  };

  const fetchDetailData = async (type: DetailView) => {
    if (!type) return;
    setDetailLoading(true);
    setDetailView(type);
    
    try {
      switch (type) {
        case "restaurants": {
          const { data } = await supabase
            .from("favorites")
            .select("created_at, restaurants(id, name, name_en, name_pt, image_url, rating, type, type_en, type_pt)")
            .eq("user_id", userId)
            .order("created_at", { ascending: false });
          setDetailData(data?.map(d => ({ ...d.restaurants, favorited_at: d.created_at })) || []);
          break;
        }
        case "activities": {
          const { data } = await supabase
            .from("subscriptions")
            .select("created_at, activities(id, title_zh, title_en, title_pt, image_url, start_date, end_date)")
            .eq("user_id", userId)
            .order("created_at", { ascending: false });
          setDetailData(data?.map(d => ({ ...d.activities, subscribed_at: d.created_at })) || []);
          break;
        }
        case "comments": {
          const { data } = await supabase
            .from("comments")
            .select("id, content, target_type, target_id, rating_value, created_at")
            .eq("user_id", userId)
            .order("created_at", { ascending: false });
          
          // Fetch target names
          if (data) {
            const restaurantIds = data.filter(c => c.target_type === "restaurant").map(c => c.target_id);
            const activityIds = data.filter(c => c.target_type === "activity").map(c => c.target_id);
            
            const [restaurantsRes, activitiesRes] = await Promise.all([
              restaurantIds.length > 0 
                ? supabase.from("restaurants").select("id, name, name_en, name_pt").in("id", restaurantIds) 
                : { data: [] },
              activityIds.length > 0 
                ? supabase.from("activities").select("id, title_zh, title_en, title_pt").in("id", activityIds) 
                : { data: [] },
            ]);
            
            const restaurantMap = Object.fromEntries((restaurantsRes.data || []).map(r => [r.id, r]));
            const activityMap = Object.fromEntries((activitiesRes.data || []).map(a => [a.id, a]));
            
            setDetailData(data.map(c => ({
              ...c,
              target: c.target_type === "restaurant" ? restaurantMap[c.target_id] : activityMap[c.target_id]
            })));
          }
          break;
        }
        case "ratings": {
          const { data } = await supabase
            .from("ratings")
            .select("id, rating_value, price_range, created_at, restaurants(id, name, name_en, name_pt)")
            .eq("user_id", userId)
            .order("created_at", { ascending: false });
          setDetailData(data?.map(d => ({ ...d, restaurant: d.restaurants })) || []);
          break;
        }
        case "addresses": {
          const { data } = await supabase
            .from("user_addresses")
            .select("*")
            .eq("user_id", userId)
            .order("is_default", { ascending: false });
          setDetailData(data || []);
          break;
        }
        case "managedRestaurants": {
          const { data } = await supabase
            .from("merchant_restaurants")
            .select("created_at, restaurants(id, name, name_en, name_pt, image_url, address)")
            .eq("user_id", userId)
            .order("created_at", { ascending: false });
          setDetailData(data?.map(d => ({ ...d.restaurants, assigned_at: d.created_at })) || []);
          break;
        }
      }
    } catch (error) {
      console.error("Error fetching detail data:", error);
      setDetailData([]);
    } finally {
      setDetailLoading(false);
    }
  };

  const getDetailTitle = (type: DetailView) => {
    switch (type) {
      case "restaurants": return t("admin.restaurantFavorites");
      case "activities": return t("admin.activityFavorites");
      case "comments": return t("admin.commentsCount");
      case "ratings": return t("admin.ratingsCount");
      case "addresses": return t("admin.addressesCount");
      case "managedRestaurants": return t("admin.managedRestaurants");
      default: return "";
    }
  };

  const getRoleBadgeColor = (role: string) => {
    switch (role) {
      case "admin":
        return "bg-primary text-primary-foreground";
      case "supervisor":
        return "bg-blue-500 text-white";
      case "merchant":
        return "bg-emerald-500 text-white";
      default:
        return "bg-muted text-muted-foreground";
    }
  };

  const getRoleIcon = (role: string) => {
    switch (role) {
      case "admin":
        return <Shield className="h-3.5 w-3.5" />;
      case "supervisor":
        return <Shield className="h-3.5 w-3.5" />;
      case "merchant":
        return <Briefcase className="h-3.5 w-3.5" />;
      default:
        return <Users className="h-3.5 w-3.5" />;
    }
  };

  const formatDate = (dateString: string | null) => {
    if (!dateString) return "-";
    return new Date(dateString).toLocaleDateString("zh-CN", {
      year: "numeric",
      month: "long",
      day: "numeric",
    });
  };

  const handleDialogClose = (open: boolean) => {
    if (!open) {
      setDetailView(null);
      setDetailData([]);
    }
    onOpenChange(open);
  };

  const renderDetailView = () => {
    if (detailLoading) {
      return (
        <div className="space-y-3">
          {[1, 2, 3].map(i => (
            <Skeleton key={i} className="h-16 w-full" />
          ))}
        </div>
      );
    }

    if (detailData.length === 0) {
      return (
        <div className="text-center py-8 text-muted-foreground">
          {t("common.noDataFound")}
        </div>
      );
    }

    switch (detailView) {
      case "restaurants":
        return (
          <div className="space-y-2">
            {detailData.map((restaurant) => (
              <Link 
                key={restaurant.id}
                to={`/restaurant/${restaurant.id}`}
                target="_blank"
                className="flex items-center gap-3 p-3 rounded-lg border hover:bg-muted/50 transition-colors"
              >
                <img 
                  src={restaurant.image_url || "https://images.unsplash.com/photo-1555939594-58d7cb561ad1?w=100&q=80"} 
                  alt={getLocalizedField(restaurant, "name")}
                  className="w-12 h-12 rounded-lg object-cover flex-shrink-0"
                />
                <div className="flex-1 min-w-0">
                  <p className="font-medium truncate">{getLocalizedField(restaurant, "name")}</p>
                  <p className="text-xs text-muted-foreground truncate">{getLocalizedField(restaurant, "type")}</p>
                </div>
                {restaurant.rating && (
                  <div className="flex items-center gap-1 text-sm">
                    <Star className="h-3.5 w-3.5 fill-yellow-500 text-yellow-500" />
                    {restaurant.rating.toFixed(1)}
                  </div>
                )}
                <ExternalLink className="h-4 w-4 text-muted-foreground flex-shrink-0" />
              </Link>
            ))}
          </div>
        );

      case "activities":
        return (
          <div className="space-y-2">
            {detailData.map((activity) => (
              <Link 
                key={activity.id}
                to={`/activity/${activity.id}`}
                target="_blank"
                className="flex items-center gap-3 p-3 rounded-lg border hover:bg-muted/50 transition-colors"
              >
                {activity.image_url && (
                  <img 
                    src={activity.image_url} 
                    alt={getLocalizedField(activity, "title")}
                    className="w-12 h-12 rounded-lg object-cover flex-shrink-0"
                  />
                )}
                <div className="flex-1 min-w-0">
                  <p className="font-medium truncate">{getLocalizedField(activity, "title")}</p>
                  <p className="text-xs text-muted-foreground">
                    {new Date(activity.start_date).toLocaleDateString()}
                    {activity.end_date && ` - ${new Date(activity.end_date).toLocaleDateString()}`}
                  </p>
                </div>
                <ExternalLink className="h-4 w-4 text-muted-foreground flex-shrink-0" />
              </Link>
            ))}
          </div>
        );

      case "comments":
        return (
          <div className="space-y-2">
            {detailData.map((comment) => (
              <Link 
                key={comment.id}
                to={`/${comment.target_type}/${comment.target_id}`}
                target="_blank"
                className="block p-3 rounded-lg border hover:bg-muted/50 transition-colors"
              >
                <div className="flex items-center justify-between mb-1">
                  <div className="flex items-center gap-2">
                    <Badge variant="outline" className="text-xs">
                      {comment.target_type === "restaurant" ? t("common.restaurant") : t("common.activity")}
                    </Badge>
                    <span className="text-sm font-medium truncate">
                      {comment.target && (comment.target_type === "restaurant" 
                        ? getLocalizedField(comment.target, "name")
                        : getLocalizedField(comment.target, "title"))}
                    </span>
                  </div>
                  {comment.rating_value && (
                    <div className="flex items-center gap-1 text-sm">
                      <Star className="h-3.5 w-3.5 fill-yellow-500 text-yellow-500" />
                      {comment.rating_value}
                    </div>
                  )}
                </div>
                <p className="text-sm text-muted-foreground line-clamp-2">{comment.content}</p>
                <p className="text-xs text-muted-foreground mt-1">
                  {formatDistanceToNow(new Date(comment.created_at), { addSuffix: true })}
                </p>
              </Link>
            ))}
          </div>
        );

      case "ratings":
        return (
          <div className="space-y-2">
            {detailData.map((rating) => (
              <Link 
                key={rating.id}
                to={`/restaurant/${rating.restaurant?.id}`}
                target="_blank"
                className="flex items-center gap-3 p-3 rounded-lg border hover:bg-muted/50 transition-colors"
              >
                <div className="flex-1 min-w-0">
                  <p className="font-medium truncate">{getLocalizedField(rating.restaurant, "name")}</p>
                  <p className="text-xs text-muted-foreground">
                    {formatDistanceToNow(new Date(rating.created_at), { addSuffix: true })}
                  </p>
                </div>
                <div className="flex items-center gap-1">
                  <Star className="h-4 w-4 fill-yellow-500 text-yellow-500" />
                  <span className="font-semibold">{rating.rating_value}</span>
                </div>
                <ExternalLink className="h-4 w-4 text-muted-foreground flex-shrink-0" />
              </Link>
            ))}
          </div>
        );

      case "addresses":
        return (
          <div className="space-y-2">
            {detailData.map((address) => (
              <div key={address.id} className="p-3 rounded-lg border">
                <div className="flex items-center gap-2 mb-1">
                  <span className="font-medium">{address.label}</span>
                  {address.is_default && (
                    <Badge variant="secondary" className="text-xs">{t("profile.defaultAddress")}</Badge>
                  )}
                </div>
                <p className="text-sm text-muted-foreground">
                  {address.street}, {address.district && `${address.district}, `}{address.city}
                </p>
                <p className="text-xs text-muted-foreground">
                  {address.postal_code && `${address.postal_code}, `}{address.country}
                </p>
                {address.phone && (
                  <p className="text-xs text-muted-foreground mt-1">
                    {address.phone_code} {address.phone}
                  </p>
                )}
              </div>
            ))}
          </div>
        );

      case "managedRestaurants":
        return (
          <div className="space-y-2">
            {detailData.map((restaurant) => (
              <Link 
                key={restaurant.id}
                to={`/restaurant/${restaurant.id}`}
                target="_blank"
                className="flex items-center gap-3 p-3 rounded-lg border hover:bg-muted/50 transition-colors"
              >
                {restaurant.image_url && (
                  <img 
                    src={restaurant.image_url} 
                    alt={getLocalizedField(restaurant, "name")}
                    className="w-12 h-12 rounded-lg object-cover flex-shrink-0"
                  />
                )}
                <div className="flex-1 min-w-0">
                  <p className="font-medium truncate">{getLocalizedField(restaurant, "name")}</p>
                  <p className="text-xs text-muted-foreground truncate">{restaurant.address}</p>
                </div>
                <ExternalLink className="h-4 w-4 text-muted-foreground flex-shrink-0" />
              </Link>
            ))}
          </div>
        );

      default:
        return null;
    }
  };

  return (
    <Dialog open={open} onOpenChange={handleDialogClose}>
      <DialogContent className="sm:max-w-[500px] max-h-[90vh] overflow-y-auto">
        {detailView ? (
          // Detail View
          <>
            <DialogHeader>
              <div className="flex items-center gap-2">
                <Button 
                  variant="ghost" 
                  size="icon" 
                  className="h-8 w-8"
                  onClick={() => {
                    setDetailView(null);
                    setDetailData([]);
                  }}
                >
                  <ArrowLeft className="h-4 w-4" />
                </Button>
                <div>
                  <DialogTitle>{getDetailTitle(detailView)}</DialogTitle>
                  <DialogDescription>
                    {profile?.display_name} - {detailData.length} {t("common.items")}
                  </DialogDescription>
                </div>
              </div>
            </DialogHeader>
            <ScrollArea className="max-h-[60vh] pr-4">
              {renderDetailView()}
            </ScrollArea>
          </>
        ) : (
          // Main Profile View
          <>
            <DialogHeader>
              <DialogTitle>{t("admin.viewProfile")}</DialogTitle>
              <DialogDescription>
                {t("admin.userProfileDetails")}
              </DialogDescription>
            </DialogHeader>

            {isLoading ? (
              <div className="space-y-4">
                <div className="flex items-center gap-4">
                  <Skeleton className="h-16 w-16 rounded-full" />
                  <div className="space-y-2">
                    <Skeleton className="h-5 w-32" />
                    <Skeleton className="h-4 w-24" />
                  </div>
                </div>
                <Skeleton className="h-20 w-full" />
                <Skeleton className="h-32 w-full" />
              </div>
            ) : profile ? (
              <div className="space-y-6">
                {/* User Header */}
                <div className="flex items-center gap-4">
                  <Avatar className="h-16 w-16 border-2 border-primary/20">
                    <AvatarImage src={profile.avatar_url || ""} alt={profile.display_name} />
                    <AvatarFallback className="text-xl bg-primary/10 text-primary">
                      {profile.display_name.charAt(0).toUpperCase()}
                    </AvatarFallback>
                  </Avatar>
                  <div className="space-y-1">
                    <h3 className="text-xl font-semibold">{profile.display_name}</h3>
                    <div className="flex items-center gap-2">
                      <Badge className={`${getRoleBadgeColor(userRole)} gap-1`}>
                        {getRoleIcon(userRole)}
                        <span className="capitalize">{userRole}</span>
                      </Badge>
                      {profile.is_banned && (
                        <Badge variant="destructive">{t("admin.banned")}</Badge>
                      )}
                    </div>
                  </div>
                </div>

                <Separator />

                {/* Contact Info */}
                <div className="space-y-3">
                  <h4 className="font-medium text-sm text-muted-foreground">{t("admin.contactInfo")}</h4>
                  <div className="grid grid-cols-1 gap-3">
                    <div className="flex items-center gap-3">
                      <Mail className="h-4 w-4 text-muted-foreground" />
                      <span className="text-sm">{email || "-"}</span>
                    </div>
                    <div className="flex items-center gap-3">
                      <Phone className="h-4 w-4 text-muted-foreground" />
                      <span className="text-sm">{profile.phone || "-"}</span>
                    </div>
                  </div>
                </div>

                {/* Application Info - Only shown if user has invitation code data */}
                {(profile.full_name || profile.nif || profile.contact || profile.address) && (
                  <>
                    <Separator />
                    <div className="space-y-3">
                      <h4 className="font-medium text-sm text-muted-foreground">{t("admin.applicationInfo", "申请信息")}</h4>
                      <div className="grid grid-cols-1 gap-3">
                        {profile.full_name && (
                          <div className="flex items-center gap-3">
                            <User className="h-4 w-4 text-muted-foreground" />
                            <div className="text-sm">
                              <p className="text-muted-foreground text-xs">{t("admin.fullName", "全名")}</p>
                              <p>{profile.full_name}</p>
                            </div>
                          </div>
                        )}
                        {profile.nif && (
                          <div className="flex items-center gap-3">
                            <FileText className="h-4 w-4 text-muted-foreground" />
                            <div className="text-sm">
                              <p className="text-muted-foreground text-xs">{t("admin.nif", "NIF")}</p>
                              <p>{profile.nif}</p>
                            </div>
                          </div>
                        )}
                        {profile.contact && (
                          <div className="flex items-center gap-3">
                            <Phone className="h-4 w-4 text-muted-foreground" />
                            <div className="text-sm">
                              <p className="text-muted-foreground text-xs">{t("admin.contactMethod", "联系方式")}</p>
                              <p>{profile.contact}</p>
                            </div>
                          </div>
                        )}
                        {profile.address && (
                          <div className="flex items-center gap-3">
                            <MapPin className="h-4 w-4 text-muted-foreground" />
                            <div className="text-sm">
                              <p className="text-muted-foreground text-xs">{t("admin.address", "地址")}</p>
                              <p>{profile.address}</p>
                            </div>
                          </div>
                        )}
                      </div>
                    </div>
                  </>
                )}

                <Separator />

                {/* Account Info */}
                <div className="space-y-3">
                  <h4 className="font-medium text-sm text-muted-foreground">{t("admin.accountInfo")}</h4>
                  <div className="grid grid-cols-2 gap-3">
                    <div className="flex items-center gap-2">
                      <Calendar className="h-4 w-4 text-muted-foreground" />
                      <div className="text-sm">
                        <p className="text-muted-foreground text-xs">{t("admin.registrationTime")}</p>
                        <p>{formatDate(profile.created_at)}</p>
                      </div>
                    </div>
                    <div className="flex items-center gap-2">
                      <Clock className="h-4 w-4 text-muted-foreground" />
                      <div className="text-sm">
                        <p className="text-muted-foreground text-xs">{t("admin.languagePreference")}</p>
                        <p>{profile.language_preference || "en"}</p>
                      </div>
                    </div>
                  </div>
                  {profile.activity_terms_agreed && (
                    <div className="flex items-center gap-2 text-sm text-emerald-600">
                      <Shield className="h-4 w-4" />
                      <span>{t("admin.agreedActivityTerms")} ({formatDate(profile.activity_terms_agreed_at)})</span>
                    </div>
                  )}
                  {profile.is_banned && profile.ban_reason && (
                    <div className="p-3 bg-destructive/10 rounded-lg">
                      <p className="text-sm text-destructive font-medium">{t("admin.banReason")}:</p>
                      <p className="text-sm text-destructive/80">{profile.ban_reason}</p>
                    </div>
                  )}
                </div>

                <Separator />

                {/* Statistics - Clickable */}
                <div className="space-y-3">
                  <h4 className="font-medium text-sm text-muted-foreground">{t("admin.userStats")}</h4>
                  <div className="grid grid-cols-3 gap-2">
                    <Card 
                      className={`bg-muted/30 transition-colors ${stats.favoriteRestaurants > 0 ? 'cursor-pointer hover:bg-muted/50' : ''}`}
                      onClick={() => stats.favoriteRestaurants > 0 && fetchDetailData("restaurants")}
                    >
                      <CardContent className="p-3 text-center relative">
                        <Heart className="h-4 w-4 mx-auto mb-1 text-red-500" />
                        <p className="text-lg font-semibold">{stats.favoriteRestaurants}</p>
                        <p className="text-xs text-muted-foreground">{t("admin.restaurantFavorites")}</p>
                        {stats.favoriteRestaurants > 0 && (
                          <ChevronRight className="h-3 w-3 absolute top-2 right-2 text-muted-foreground" />
                        )}
                      </CardContent>
                    </Card>
                    <Card 
                      className={`bg-muted/30 transition-colors ${stats.favoriteActivities > 0 ? 'cursor-pointer hover:bg-muted/50' : ''}`}
                      onClick={() => stats.favoriteActivities > 0 && fetchDetailData("activities")}
                    >
                      <CardContent className="p-3 text-center relative">
                        <Heart className="h-4 w-4 mx-auto mb-1 text-primary" />
                        <p className="text-lg font-semibold">{stats.favoriteActivities}</p>
                        <p className="text-xs text-muted-foreground">{t("admin.activityFavorites")}</p>
                        {stats.favoriteActivities > 0 && (
                          <ChevronRight className="h-3 w-3 absolute top-2 right-2 text-muted-foreground" />
                        )}
                      </CardContent>
                    </Card>
                    <Card 
                      className={`bg-muted/30 transition-colors ${stats.comments > 0 ? 'cursor-pointer hover:bg-muted/50' : ''}`}
                      onClick={() => stats.comments > 0 && fetchDetailData("comments")}
                    >
                      <CardContent className="p-3 text-center relative">
                        <MessageSquare className="h-4 w-4 mx-auto mb-1 text-blue-500" />
                        <p className="text-lg font-semibold">{stats.comments}</p>
                        <p className="text-xs text-muted-foreground">{t("admin.commentsCount")}</p>
                        {stats.comments > 0 && (
                          <ChevronRight className="h-3 w-3 absolute top-2 right-2 text-muted-foreground" />
                        )}
                      </CardContent>
                    </Card>
                    <Card 
                      className={`bg-muted/30 transition-colors ${stats.ratings > 0 ? 'cursor-pointer hover:bg-muted/50' : ''}`}
                      onClick={() => stats.ratings > 0 && fetchDetailData("ratings")}
                    >
                      <CardContent className="p-3 text-center relative">
                        <Star className="h-4 w-4 mx-auto mb-1 text-yellow-500" />
                        <p className="text-lg font-semibold">{stats.ratings}</p>
                        <p className="text-xs text-muted-foreground">{t("admin.ratingsCount")}</p>
                        {stats.ratings > 0 && (
                          <ChevronRight className="h-3 w-3 absolute top-2 right-2 text-muted-foreground" />
                        )}
                      </CardContent>
                    </Card>
                    <Card 
                      className={`bg-muted/30 transition-colors ${stats.addresses > 0 ? 'cursor-pointer hover:bg-muted/50' : ''}`}
                      onClick={() => stats.addresses > 0 && fetchDetailData("addresses")}
                    >
                      <CardContent className="p-3 text-center relative">
                        <MapPin className="h-4 w-4 mx-auto mb-1 text-emerald-500" />
                        <p className="text-lg font-semibold">{stats.addresses}</p>
                        <p className="text-xs text-muted-foreground">{t("admin.addressesCount")}</p>
                        {stats.addresses > 0 && (
                          <ChevronRight className="h-3 w-3 absolute top-2 right-2 text-muted-foreground" />
                        )}
                      </CardContent>
                    </Card>
                    {(userRole === "merchant" || userRole === "supervisor") && (
                      <Card 
                        className={`bg-muted/30 transition-colors ${stats.restaurants > 0 ? 'cursor-pointer hover:bg-muted/50' : ''}`}
                        onClick={() => stats.restaurants > 0 && fetchDetailData("managedRestaurants")}
                      >
                        <CardContent className="p-3 text-center relative">
                          <Store className="h-4 w-4 mx-auto mb-1 text-orange-500" />
                          <p className="text-lg font-semibold">{stats.restaurants}</p>
                          <p className="text-xs text-muted-foreground">{t("admin.managedRestaurants")}</p>
                          {stats.restaurants > 0 && (
                            <ChevronRight className="h-3 w-3 absolute top-2 right-2 text-muted-foreground" />
                          )}
                        </CardContent>
                      </Card>
                    )}
                  </div>
                </div>
              </div>
            ) : (
              <div className="text-center py-8 text-muted-foreground">
                {t("common.noDataFound")}
              </div>
            )}
          </>
        )}
      </DialogContent>
    </Dialog>
  );
}
